
.cart {
    width: 100%;
    max-width: 330px;
    background-color: white;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    padding: 100px 20px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transform: translate(110%, 0);
    transition: all 400ms ease;
}

.cartItens{
    flex-grow: 1;
    overflow: auto;
}

.cartResume {
    font-size: 1.0rem;
    font-weight: 580;
    padding: 35px 0 15px;
    border-top: 1px solid #ddd;
}

.cartItens .cartItem:last-child {
    border-bottom: none;
}

.cartActive {
    transform: translate(0, 0);
}